import React from 'react'

const Introduction = () => (
  <div className='m-4'>

    <hr />
    <br />

    <h5>
      Kalkulator AGcH to projekt zrobiony w ramach hobby - liczy wskanik punktw
      rekrutacyjnych, ale by pisany kilka lat temu, nie jest ju wspierany i posuguje si danymi na temat kierunkw
      sprzed kilku lat.
      <br />
      <br />
      Cz wynikw moe si pokry z obecnymi zasadami, ale na pewno nie wszystko - sorry \_()_/
    </h5>

    <br />
    <hr />
    <br />

    <h3>
      Instrukszon
    </h3>

    <br />

    <ol>
      <li>
        Dodaj przedmioty gwne (czyli np. matematyka, fizyka), ktre pisae na maturze. Podaj wynik procentowy
        matury.
      </li>
      <li>
        Powtrz z jzykami.
      </li>
      <li>
        Sprawd wynik!
      </li>
    </ol>

    <br />
    <hr />

  </div>
)

export default Introduction



